import React, { Component } from 'react';
import Classes from './App.css';
import Radium, {StyleRoot} from "radium";
import Person from "./Person/Person.js";



class App extends Component {
  state = {
    persons: [
      {id:"1", name: "Lukas", age:"25"},
      {id:"2", name: "Pupis", age:"24"},
      {id:"3", name: "Krabas", age:"35"}
    ],
    otherState: "some other value",
    showPersons: false
  }

  swichNameHandler = (newName) => {
    // DON'T DO THIS this.state.persons[0].name = "Stabas";
    this.setState({
      persons: [
      { name: newName, age:"25"},
      { name: "Pupis", age:"24"},
      { name: "Krabas", age:"35"}
    ]
    })
  }

  nameChangedHandler = (event, id) => {
    const personIndex = this.state.persons.findIndex(per => {
      return per.id === id;
    });

    const person = {
      ...this.state.persons[personIndex]
    };

    person.name = event.target.value;

    const persons = [...this.state.persons];
    persons[personIndex] = person;

    // other way to change state from input
    this.setState({
      persons:persons
    })
  } // super complex needs more digging (59 flexible lists)

  deletePersonHandler = (personIndex) => {
    const persons = this.state.persons
    persons.splice(personIndex, 1); // removes at all. cant get back that element.
    this.setState({persons:persons});
  }

  togglePersonsHandler = () => {
    const doseShow = this.state.showPersons;
    this.setState({showPersons:!doseShow});
  }

  render() {
    const style = {
      backgroundColor:"green",
      color:"white",
      font: "inherit",
      border: "1px solid blue",
      padding: "8px",
      cursor: "pointer",
      ":hover":{
        backgroundColor: "lightgreen",
        color:"black"
      }
    };

    let persons = null;

    if (this.state.showPersons){
      persons = (
        <div >
          {this.state.persons.map((person, index) => {
            return <Person 
              click={()=>this.deletePersonHandler(index)}
              name={person.name} 
              age={person.age} //3 because of map method. this is loop thru all elements
              key={person.id}  // this is only for virtual dom efficiency
              changed={(event)=>this.nameChangedHandler(event, person.id)}/>
          })}
          </div>
      );

      style.backgroundColor="red";
      style[":hover"]={
        backgroundColor:"salmon",
        color:"black"
      }
    }

    let classes = [];
    if (this.state.persons.length <= 2) {
      classes.push("red");
    }
    if (this.state.persons.length <= 1) {
      classes.push(Classes.bold);
    }


    return (
      <StyleRoot>
      <div className={Classes.App}>
        <h1>Hi, I'am a React App</h1>
        <p className={classes.join(" ")}>This is really working</p>
        <button 
          style={style}
          onClick={this.togglePersonsHandler}>Toggle Persons
        </button>
        {persons}
        {/* prefered way to show parts of data (elements) */}
      </div>
      </StyleRoot>
    );
  }
}

export default Radium(App);



